@import './_base';

/*Custom Font*/
@font-face {
  font-family: After-Night;
  font-display: swap;
  unicode-range: U+000-5FF;
  src:
    local('After-Night'),
    url('/fonts/AfterNight/After-Night.woff') format('woff'),
    // url('https://webassets.policypal.com/fonts/AfterNight/After-Night.ttf') format('truetype'),
    // url('./assets/fonts/AfterNight/After-Night.woff') format('woff'),
    // url('./assets/fonts/AfterNight/After-Night.ttf') format('truetype');
}
@font-face{
  font-family: Lato;
  font-display: swap;
  unicode-range: U+000-5FF;
  src:
    local('Lato-Regular'),
    url('https://fonts.googleapis.com/css?family=Lato');
}
// OPEN SANS
@font-face {
  font-family: 'Open Sans';
  font-display: swap;
  unicode-range: U+000-5FF;
  src:
      local('OpenSans-SemiBold'),
      url('https://fonts.googleapis.com/css?family=Open+Sans');
  font-weight: 600;
  font-style: normal;
  font-variant:normal;
}

@font-face {
  font-family: 'Open Sans';
  font-display: swap;
  unicode-range: U+000-5FF;
  src:
      local('OpenSans-Bold'),
      url('https://fonts.googleapis.com/css?family=Open+Sans');
  font-weight: bold;
  font-style: normal;
  font-variant:normal;
}

@font-face {
  font-family: 'Open Sans';
  font-display: swap;
  unicode-range: U+000-5FF;
  src:
      local('OpenSans-Regular'),
      url('https://fonts.googleapis.com/css?family=Open+Sans');
  font-weight: normal;
  font-style: normal;
  font-variant:normal;
}

@font-face {
  font-family: 'Open Sans';
  font-display: swap;
  unicode-range: U+000-5FF;
  src:
      local('OpenSans-ExtraBold'),
      url('https://fonts.googleapis.com/css?family=Open+Sans');
  font-weight: 800;
  font-style: normal;
  font-variant:normal;
}

@font-face {
  font-family: 'Open Sans';
  font-display: swap;
  unicode-range: U+000-5FF;
  src:
      local('OpenSans-Light'),
      url('https://fonts.googleapis.com/css?family=Open+Sans');
  font-weight: 300;
  font-style: normal;
  font-variant:normal;
}


h1 {
  font-family: 'Open Sans';
  font-weight: $font-weight-heavy;
  font-size: $font-size-extra-tiny * 3;

  color: $color-input-text;
  @media screen and (max-width: 768px) {
    font-size: $font-size-extra-tiny * 2;
  }
}

h2 {
  font-family: 'Open Sans';
  font-weight: $font-weight-bold;
  font-size: $font-size-extra-tiny * 3;

  color: $color-input-text;

  @media screen and (max-width: 768px) {
    font-size: $font-size-extra-tiny * 2;
  }
}

h3 {
  font-family: 'Open Sans';
  font-weight: $font-weight-bold;
  font-size: $font-size-regular;
  color: $color-input-text;

  @media screen and (max-width: 768px) {
    font-size: $font-size-smallest;
  }
}

h5 {
  font-family: 'Open Sans';
  font-weight: $font-weight-normal;
  font-size: $font-size-tiny;

  color: $color-input-form-landing-desc2;
}

h6 {
  font-family: 'Open Sans';
  font-weight: $font-weight-semibold;
  font-size: $font-size-smallest;
  color: $color-input-form-landing-desc2;

  @media screen and (max-width: 768px) {
    font-size: $font-size-extra-tiny;
  }
}

.h7 {
  font-family: 'Open Sans';
  font-weight: $font-weight-semibold;
  font-size: $font-size-tiny;

  color: $color-other-desc;
}

button {
  border: none;
  padding: 0;
  background: transparent;
  transition: all 0.1s ease;
  &:hover {
    cursor: pointer;
  }

  &:active {
    transform: scaleY(1.1);
  }

  &:focus,
  &:active {
    outline: none;
  }
}

.noscroll {
  overflow: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  // to fix the double scroll-y
  overflow-x: hidden !important;
  .App{
    overflow-y: visible !important;
  }
}

hr {
  background-color: #c8e0e4;
}

a {
  color: $color-hover;

  &:hover {
    color: $color-hover-alt;
  }
}

.no-padding,
.container-fluid{
  padding: 0;
}

.main-container-fluid {
  padding-top: 60px;
  overflow: hidden;
  
  @media screen and(min-width: 802px){
    padding-top: 84px;
  }
}

// Form stuffs
.form-seperator {
  margin-top: 34px;
  font-size: $font-size-big;
  font-weight: $font-weight-bold;
  color: $color-title;
}

@media screen and (min-width: 576px) {
  .form-right-padding-small,
  .form-right-padding {
    padding-right: 10px !important;
  }
  .form-left-padding-small,
  .form-left-padding {
    padding-left: 10px !important;
  }
}

@media screen and (min-width: 1040px) {
  .form-right-padding {
    padding-right: 20px !important;
  }
  .form-left-padding {
    padding-left: 20px !important;
  }
}

.modal-backdrop {
  background-image: linear-gradient(134deg, rgba(0, 215, 175, 0.75), rgba(70, 115, 122, 0.75));

  &.show {
    opacity: 0.75;
  }
}

.modal-open {
  overflow: hidden !important;
}

.modal-dialog {
  box-shadow: 0 30px 40px 0 rgba(0, 0, 0, 0.15);
  border-radius: 8px;

  .modal-content {
    padding: 10px 40px 40px 40px;
    border: none;

    @media screen and (max-width: 576px) {
      padding: 5px 15px 15px 15px;
    }
  }
}

.input-label {
  min-height: 18px;
  font-weight: $font-weight-bold;
  font-size: $font-size-tiny;

  color: $color-title;
  margin: 0;
  margin-left: 7px;

  transform: translateY(8px);

  @media screen and (max-width: 576px) {
      min-height: 0;
  }
}

.cashback-earned-container {
  margin: auto;

  p,
  span {
    font-weight: $font-weight-semibold;
    font-size: $font-size-tiny;

    color: $color-input-form-landing-desc;
    margin-left: 5px;
  }
}

.blue-modal-backdrop {
  background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
  opacity: 0.9;
}

.flow-visible {
  opacity: 1;
  transition: opacity 0.2s ease;
}

.flow-invisible {
  opacity: 0;
  transition: opacity 0.2s ease;
}

.alice-carousel {
  max-width: calc(100vw - 30px);
}
